                </div>
                <div class="quicklogin-item">
                    <i class="iconfont icon-circular-Weibo"></i>
                    <span>微博</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'login',
  data() { 
    return {
        iconFlag: false,
        loginFlag: false,
        tel:'',
        idenifyscode:""
    }
  },
  methods: {
    back(){
       this.$router.go(-1);
   },
   loginIn(){
       if(this.tel!=""){
            let userinfo={
                tel:this.tel,
                code:this.idenifyscode
            };
            window.localStorage.setItem('userinfo',JSON.stringify(userinfo));
            this.$router.go(-1);
       }
      
   }
 }
}
</script>

